import React from "react";
import { Select } from "antd";
import { useQuery } from "react-query3";
import { Role } from "../../types";

export default function RoleSelectBox(props) {
  const { data, isLoading } = useQuery<Role[]>("/role");
  return (
    <Select {...props} loading={isLoading}>
      <Select.Option value={null}>{null}</Select.Option>
      <Select.Option value="policy">Policy Defined</Select.Option>
      {data?.map((role) => (
        <Select.Option key={role.name} value={role.name}>{role.name}</Select.Option>
      ))}
    </Select>
  );
}
